$columns: 12; // Number of columns in the grid system

.container {
	margin: 0 auto;
	max-width: 960px;
}

.container-fluid {
	max-width: 100%;
}

.row {
	display: grid;
	grid-template-columns: 290px 1fr 1fr;
}

.row.case-2-cols {
	display: grid;
	grid-template-columns: 1fr 1fr;
}



[class^="col-"] {
	display: grid;
}


.col-left {
	grid-auto-rows: auto;
}

.col_box {
	display: grid;
}

@media only screen and (max-width: 768px) {
	.row {
		grid-template-columns: 1fr !important;
	}
}